
.Form{
    margin:0;
    padding:0;
    display: flex;
    flex-direction:column;
    justify-content: center;
    width: 30vw;
    font-family: 'Poppins', sans-serif;
}
@media screen and (max-width: 800px) {
    .Form {
      display: flex; 
      flex-direction: column; 
      width: 90vw;
       
    }
  }
.Wrapper{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 28vw;
}
@media screen and (max-width: 800px) {
    .Wrapper {
      display: flex; 
      flex-direction: column; 
      justify-content: center;
      width: 90vw;
       
    }
  }

.Header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 6vh;
    border-radius: 10px;
    background-color: hsl(248, 32%, 49%);
}
@media screen and (max-width: 800px) {
    .Header {
      display: flex;
    flex-direction: row;
    justify-content: center;
      text-align: center;
     width: 90vw;
     height: 10vh;
    }
  }
.Offer{
    display: flex;
    flex-direction: row;
    justify-content: center; 
    text-align: center;
    font-size: smaller;
    color: white;
    font-weight: 400;
}
@media screen and (max-width: 800px) {
    .Offer {
    text-align: center;
     width: 60vw;
     height: vh;
    }
  }

input{  
    margin-top: 4vh;
    margin-left:  2vh;
    width: 25vw;
    height: 4vh;
    border: 1px solid hsl(0, 100%, 74%);
    border-radius: 5px;
    font-family: Poppins, sans-serif;
    color: hsl(248, 32%, 49%);
}
@media screen and (max-width: 800px) {
    input {
     width: 88vw;
     margin-left: 0;
     margin-right: 0;
       
       
    }
  }
label{
    float: right;
    margin-right: 2vw;
    height: 2vh;
    font-size: smaller;
    width: 20vw;
    text-align: right;
    color: hsl(0, 100%, 74%);

}
@media screen and (max-width: 800px) {
    label {
    float: left;
    display: block;
    text-align: center;
     width: 90vw;
     margin-left: 0;
     margin-right: 0;
       
       
    }
  }
.FormContainer{
    display: flex;
    flex-direction: row;
    justify-content: center;
    height: 62vh;
    background-color: white;
    margin-top: 2vh;
    border-radius: 5px;
}
@media screen and (max-width: 800px) {
    .FormContainer {
     width: 90vw;
     height: auto;
       
    }
  }
form{
    height: 50vh;
    
}
@media screen and (max-width: 800px) {
    form {
     width: 90vw;
     height: auto;
       
    }
  }
.Form button{
    width: 24vw;
    border: none;
    background-color: transparent;
}

.Claim{
    float: left;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 4vh 2vh;
    height: 6vh;
    width: 26vw;
    border-radius: 5px;
    background-color: hsl(154, 59%, 51%);
}
@media screen and (max-width: 800px) {
    .Claim {
     width: 90vw;
     margin-left: 0;
     margin-right: 0;
    }
  }
  .SuccessMessage{
    text-align: center;;
    color:white;
    background-color: hsl(154, 59%, 51%);
    border-radius: 5px;

  }
.Claim button{
    display: flex;
    flex-direction: row;
    justify-content: center;
    font-size: small;
    color: white;
    font-weight: 700;
}
@media screen and (max-width: 800px) {
    .Claim button {
     width: 90vw;
     margin-left: 0;
     margin-right: 0;
       
    }
  }
  form p{
    padding-right: 1vw;
    text-align: right;
    font-size: 10px;
    font-weight: 400;
    color: hsl(246, 25%, 77%);
  }
  form p b{
    color: hsl(0, 100%, 74%);
  }
