@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap');

body {
  margin: 0;
  padding: 0;
  background-image: url('./bg-intro-desktop.png'), linear-gradient(to right, rgb(255, 122, 122), rgb(255, 122, 122));
  font-family: 'Poppins', sans-serif;
  
  
}
@media screen and (max-width: 800px) {
  body {
    background-image: url('./bg-intro-mobile.png'), linear-gradient(to right, rgb(255, 122, 122), rgb(255, 122, 122));
    
     
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App {
  display: flex;  
  justify-content: center;
  height: auto;
 }

@media screen and (max-width: 800px) {
  .App {
    display: flex; 
    flex-direction: column; 
    justify-content: centert;
    padding: 2vh;
    width: 90vw;
    
     
  }
}

.Content{
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 40vw;
  padding-right: 1vw;
}

@media screen and (max-width: 800px) {
  .Content {
    display: flex; 
    flex-direction: column; 
    justify-content: center;
    width: 90vw;
         
  }
}

article{
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  margin-top: auto;
  color: white;
}
article h1{
  font-weight: 700;
  font-size: 44px;
}
@media screen and (max-width: 800px) {
  .article h1{
    font-size: 30px;
  }
}
article p{
  font-weight: 600;
  font-size: 16px;
}


